.main-title {
  @include font(700, 27px, 30px);
  color: $color-text-title;
  background-color: $color-bg-darken;
  min-height: 177px;
  padding-top: 73px;
  position: relative;

  &--index {
    background-image: url("../img/hero-mobile2x1.jpg");
    background-repeat: no-repeat;
    background-position: center -17px;
    background-color: #f9f9f9;
    min-height: 490px;
    padding: 73px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.main-title__text {
  @include font(700, 27px, 30px);
  color: $color-text-title;
  text-align: center;
  margin: 0 auto;
  padding: 0 30px;
  width: $mobile-width;
}

@media (min-width: $tablet-width) {
  .main-title__text {
    @include font(700, 43px, 50px);
    width: $tablet-width;
  }

  .main-title {
    min-height: 150px;
    padding-top: 14px;

    &--index {
      min-height: 658px;
      background-image: url("../img/hero-desktop.jpg");
      background-position: bottom;
      padding-top: 68px;
    }
  }

  .main-title__mask {
    position: absolute;
    bottom: 0;
    background-image: url("../../img/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
    width: 100%;
    height: 24px;
  }
}

@media (min-width: $desktop-width) {
  .main-title__text {
    @include font(700, 53px, 57px);
    width: $desktop-width;
  }

  .main-title {
    min-height: 184px;

    &--index {
      min-height: 675px;
      padding: 71px 0 17px;
      background-position: center -89px;
    }

    .main-title__mask {
      background-image: url("../../img/zigzag-fill-desktop.svg");
      height: 34px;
    }
  }
}
